import './styling/main.scss';

import React from 'react';
import { createBrowserRouter, RouterProvider, redirect, Navigate } from 'react-router-dom';
import Login from './pages/login';
import Dashboard from './pages/dashboard';
import Settings from './pages/settings';

// Helper function to check if the user is authenticated
const isAuthenticated = () => {
  return !!localStorage.getItem('token');
};

// Define a function to handle redirection for unauthenticated users
const requireAuth = () => {
  return isAuthenticated() ? true : redirect('/login');
};

// Create a router instance using createBrowserRouter
const router = createBrowserRouter([
  {
    path: "/login",
    element: <Login />,
  },
  {
    path: "/dashboard",
    element: <Dashboard />,
    loader: requireAuth,
  },
  {
    path: "/parameters",
    element: <Settings />,
    loader: requireAuth,
  },
  {
    path: "/",
    element: <Navigate to="/login" replace />,
  },
]);

const App = () => {
  return (
    <div className="container">
      <RouterProvider router={router} />
    </div>
  );
};

export default App;